body {
  background: $primary-bg;
}
.app {
  display: grid;
  grid-template-columns: 100px 1fr;
  grid-template-rows: 115px 1fr;
  grid-template-areas:
    "nav header"
    "nav main";
  max-width: 1280px;
  min-height: 880px;
  margin: 50px auto 0;
  border-radius: $rounded;
  @include md {
    grid-template-columns: 768px;
    grid-template-rows: 1fr;
    grid-template-areas:
      "nav"
      "header"
      "main";
  }
}
